import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { SafeResourceUrl } from "@angular/platform-browser";
import { ToastController } from "@ionic/angular";
import { environment } from "src/environments/environment";

import { User } from "./../models/user";
import { AuthService } from "./auth.service";
import { RestService } from "./rest.service";
import { UploadService } from "./upload.service";

const httpOptions = {
  headers: new HttpHeaders({ "Content-Type": "application/json" })
};

@Injectable({ providedIn: "root" })
export class UserService {
  constructor(
    private rest: RestService,
    private uploadSvc: UploadService,
    private auth: AuthService,
    private toast: ToastController,
    private http: HttpClient
  ) {}

  async getUser(id: User["id"] | User["username"]): Promise<User> {
    try {
      return (await this.rest.get(`user/${id}`).toPromise()) as User;
    } catch (e) {
      throw new Error("No se puede obtener el usuario");
    }
  }

  async getPublicUser(id: User["id"] | User["username"]): Promise<User> {
    try {
      return (await this.http
        .get(`${environment.root}api/public-user/${id}`, httpOptions)
        .toPromise()) as User;
    } catch (e) {
      throw new Error("No se puede obtener el usuario");
    }
  }

  async updateUser(user: User): Promise<User> {
    try {
      user = (await this.rest.put("user", user).toPromise()) as User;
      this.auth.setAuthUser(user);
      return user;
    } catch (e) {
      throw new Error("No se puede actualizar el usuario");
    }
  }

  async uploadAvatar(file: Blob) {
    const formData: FormData = new FormData();
    formData.set("avatar", file);
    const user = (await this.uploadSvc.upload("avatar", formData)) as User;
    localStorage.setItem("currentUser", JSON.stringify(user));
    return user;
  }

  async setAvatar(src: SafeResourceUrl) {
    try {
      const user = (await this.rest
        .put("avatar", { avatar: src })
        .toPromise()) as User;
      this.auth.setAuthUser(user);
      return user;
    } catch (e) {
      throw new Error("No se puede actualizar el usuario");
    }
  }

  async deleteAvatar(src: SafeResourceUrl) {
    try {
      const user = (await this.rest
        .put("delete-avatar", { avatar: src })
        .toPromise()) as User;
      this.auth.setAuthUser(user);
      return user;
    } catch (e) {
      throw new Error("No se puede actualizar el usuario");
    }
  }

  async setCoordinates(longitude, latitude) {
    return (await this.rest
      .put("coordinates", { longitude, latitude })
      .toPromise()) as Promise<User["coordinates"]>;
  }

  getRadarUsers(
    page = 1,
    ratio = -1,
    options?: { identity: boolean; range: boolean; connection: boolean }
  ) {
    return this.rest
      .put("radar", { page, ratio, options })
      .toPromise() as Promise<User[]>;
  }

  searchUsers(query: string, order: "distance" | "match", page: number) {
    return this.rest
      .post(`search?page=${page}`, { query, order })
      .toPromise() as Promise<User[]>;
  }

  searchUsernames(query: string) {
    return this.rest.get(`search-usernames/${query}`).toPromise() as Promise<
      User[]
    >;
  }

  activateUser(verification_code: string) {
    return this.rest
      .put("activation", { verification_code })
      .toPromise() as Promise<User>;
  }

  resendActivationEmail() {
    return this.rest.get("activation");
  }

  disableUser(password: string, note: string) {
    return this.rest
      .put("disable", {
        password,
        note
      })
      .toPromise() as Promise<User>;
  }

  removeAccount(password: string, note: string) {
    return this.rest
      .put("remove-account", {
        password,
        note
      })
      .toPromise() as Promise<User>;
  }

  changePassword(old_password: string, new_password: string) {
    return this.rest
      .put("password", {
        old_password,
        new_password
      })
      .toPromise() as Promise<User>;
  }

  changeEmail(old_email: string, new_email: string) {
    return this.rest
      .put("email", {
        old_email,
        new_email
      })
      .toPromise() as Promise<User>;
  }

  changeUsername(new_username: string) {
    return this.rest
      .put("username", {
        new_username
      })
      .toPromise() as Promise<User>;
  }

  getLikes() {
    return this.rest.get("likes").toPromise() as Promise<User[]>;
  }

  like(id: User["id"]) {
    return this.rest.put("like", { user: id }).toPromise() as Promise<User>;
  }

  unlike(id: User["id"]) {
    return this.rest.delete(`like/${id}`).toPromise() as Promise<User>;
  }

  getBlocks() {
    return this.rest.get("blocks").toPromise() as Promise<User[]>;
  }

  block(id: User["id"], note?: string) {
    return this.rest.put("block", { user: id, note }).toPromise();
  }

  unblock(id: User["id"]) {
    return this.rest.delete(`block/${id}`).toPromise() as Promise<User[]>;
  }

  report(id: User["id"], note?: string) {
    return this.rest.put("report", { user: id, note }).toPromise();
  }

  getHides() {
    return this.rest.get("hides").toPromise() as Promise<User[]>;
  }

  hide(id: User["id"]) {
    return this.rest.put("hide", { user: id }).toPromise();
  }

  view(id: User["id"]) {
    this.rest.put("view", { user: id }).toPromise();
  }

  unhide(id: User["id"]) {
    return this.rest.delete(`hide/${id}`).toPromise() as Promise<User[]>;
  }

  async showRole(user: User) {
    let message = "";
    if (user.roles?.includes("ROLE_ADMIN")) {
      message = "Administrador";
    } else if (user.roles?.includes("ROLE_MASTER")) {
      message = "Moderador";
    } else if (user.roles?.includes("ROLE_PATREON")) {
      message = "Patreon";
    } else if (user.roles.includes("ROLE_DEMO")) {
      message = "FrikiBot";
    } else if (user.verified) {
      message = "Usuario verificado";
    }

    (
      await this.toast.create({
        message,
        duration: 1500,
        position: "middle",
        color: this.getRoleColor(user)
      })
    ).present();
  }

  getRoleColor(user?: User) {
    if (!user) {
      user = this.auth.currentUserValue;
    }

    if (user.roles.includes("ROLE_ADMIN")) {
      return "danger";
    } else if (user.roles.includes("ROLE_MASTER")) {
      return "tertiary";
    } else if (user.roles.includes("ROLE_PATREON")) {
      return "patreon";
    } else if (user.roles.includes("ROLE_DEMO")) {
      return "medium";
    } else if (user.verified) {
      return "secondary";
    }
    return "light";
  }

  getRoleIcon(user?: User) {
    if (!user) {
      user = this.auth.currentUserValue;
    }

    if (user.roles.includes("ROLE_ADMIN")) {
      return "shield-checkmark";
    } else if (user.roles.includes("ROLE_MASTER")) {
      return "shield-checkmark";
    } else if (user.roles.includes("ROLE_PATREON")) {
      return "/assets/icon/patreon_coral.svg";
    } else if (user.roles.includes("ROLE_DEMO")) {
      return "/assets/icon/smart_toy_white_24dp.svg";
    } else if (user.verified) {
      return "checkmark-circle";
    }

    return false;
  }

  async linkToPatreon(code: string) {
    await this.rest.put("link-patreon", { oauth_code: code }).toPromise();
  }

  getOrientations() {
    return [
      "Heterosexual",
      "Homosexual",
      "Bisexual",
      "Pansexual",
      "Queer",
      "Demisexual",
      "Sapiosexual",
      "Asexual"
    ];
  }

  getPronouns() {
    return ["El", "Ella", "Elle", "Elli"];
  }

  getRelationships() {
    return ["Mongama", "No-mongama"];
  }

  getStatus() {
    return ["Soltero", "Saliendo con alguien", "Pareja estable", "Casado"];
  }

  getGenders() {
    return [
      "Mujer",
      "Hombre",
      "Mujer transgnero",
      "Hombre transgnero",
      "Agnero",
      "Andrgino",
      "Gnero fluido",
      "Bignero",
      "No-binario",
      "No conforme",
      "Pangnero",
      "Polignero",
      "Intergnero"
    ];
  }

  getConnections() {
    return [
      "Amistad",
      "Sexo ocasional",
      "Amistad con derechos",
      "Pareja formal"
    ];
  }
}
